import * as React from 'react';
import { useState, useEffect } from 'react';
import { navigate } from 'gatsby'; // Import navigate from Gatsby
import Layout from '../components/Layout';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';

// Alert component for Snackbar
const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const IndexPage = () => {
  const [text, setText] = useState('');
  const fullText = 'App with AI';
  const [index, setIndex] = useState(0);
  const [openSnackbar, setOpenSnackbar] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState('');
  const [snackbarSeverity, setSnackbarSeverity] = useState('info'); // 'error', 'warning', 'info', 'success'

  // Typing animation effect
  useEffect(() => {
    if (index < fullText.length) {
      const timeout = setTimeout(() => {
        setText((prevText) => prevText + fullText[index]);
        setIndex((prevIndex) => prevIndex + 1);
      }, 100); // Adjust typing speed here (100ms per character)

      return () => clearTimeout(timeout);
    }
  }, [index, fullText]);

  // Handle Snackbar close
  const handleCloseSnackbar = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpenSnackbar(false);
  };

  // Handle Start button click
  const handleStart = () => {
    // Generate a random string for the URL
    const randomString = Math.random().toString(36).substring(2, 15);
    // Navigate to the new route
    navigate(`/app/${randomString}`);
  };

  return (
    <Layout>
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'center',
          minHeight: '80vh',
          textAlign: 'center',
        }}
      >
        {/* Heading with Typing Animation */}
        <Typography
          variant="h3"
          component="h1"
          gutterBottom
          sx={{
            overflow: 'hidden', // Hide overflow
            borderRight: '0.15em solid orange', // Cursor effect
            whiteSpace: 'nowrap', // Keep text in one line
            margin: '0 auto', // Center the text
            letterSpacing: '0.15em', // Spacing between letters
          }}
        >
          {text}
        </Typography>

        {/* Start Button */}
        <Button
          variant="contained"
          color="primary"
          size="large"
          onClick={handleStart} // Handle button click
        >
          Start
        </Button>
      </Box>

      {/* Snackbar for showing messages */}
      <Snackbar
        open={openSnackbar}
        autoHideDuration={6000}
        onClose={handleCloseSnackbar}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert onClose={handleCloseSnackbar} severity={snackbarSeverity}>
          {snackbarMessage}
        </Alert>
      </Snackbar>
    </Layout>
  );
};

export default IndexPage;